import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "../screens/styles.module.css";
import { updateAInfo } from "../actions/profileActions";
import Loading from "./UI/Loading";
import banks from "./bank";

const AccountInfo = ({ user, history }) => {
  const dispatch = useDispatch();
  const ainfo = useSelector((state) => state.ainfo);
  const { loading, error, success } = ainfo;

  useEffect(() => {
    if (!user) {
      history.push("/profile");
    }
  }, [user, history]);

  const [bank, setBank] = useState(user && user.bank);
  const [accountNumber, setAccountNumber] = useState(
    user && user.accountNumber
  );

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(updateAInfo(bank, accountNumber));
  };

  return (
    <div className={styles.forms}>
      {error && <div className="alert alert-info">{error}</div>}
      {success && (
        <div className="alert alert-success">Account Information Saved</div>
      )}
      {loading ? (
        <Loading />
      ) : (
        <>
          <form onSubmit={submitHandler}>
            <div className={`row`}>
              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Bank Name</label>
                <select
                  className="form-control"
                  value={bank}
                  onChange={(e) => setBank(e.target.value)}
                >
                  <option>{bank ? bank : "Select ....."}</option>
                  {banks.map((item) => (
                    <option value={item.name} key={item.id}>
                      {item.name}
                    </option>
                  ))}
                </select>
              </div>

              <div className={`col-md-6 ${styles.formPadding}`}>
                <label>Account Number</label>
                <input
                  type="text"
                  className="form-control"
                  value={accountNumber}
                  onChange={(e) => setAccountNumber(e.target.value)}
                />
              </div>

              <div className={`col-md-12 ${styles.formPadding}`}>
                <label />
                <input
                  type="submit"
                  className="btn btn-dark form-control"
                  value="Save"
                />
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  );
};

export default AccountInfo;
